.backdrop {
  width: 100vw;
  height: 100vh;
  opacity: 1;
  overflow: overlay;
  background-color: rgba(0, 0, 0, .42);
  transition: opacity .1s;
  position: fixed;
  top: 0;
  left: 0;
}

