  opacity: 0;
  pointer-events: none;
}

.modal {
  background-color: var(--modal-bg-color);
  width: 280px;
  padding: 48px 20px 40px;
  transition: transform .1s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 0 30px 4px rgba(0, 0, 0, .42);
}

